export default function mainIframeManager({
    partnerConfig = {},
}) {
    const queryParams = new URL(window.location).searchParams.entries();
    const {provider} = Object.fromEntries(queryParams);

    // eslint-disable-next-line no-console
    console.info('iframeManager remoteConfiguration', partnerConfig);

    // const signalReadyWithProvider = function () {
    //     window.top.postMessage(window.provider, '*');
    // };
    switch (provider) {
        case 'au.com.abc':
            import('./au.com.abc').then(({default: Provider}) => {
                window.provider = new Provider(partnerConfig.providers?.['au.com.abc']);
            });
            break;

        case 'au.com.sbs':
            import('./au.com.sbs').then(({default: Provider}) => {
                window.provider = new Provider(partnerConfig.providers?.['au.com.sbs']);
            });
            break;

        case 'au.com.seven':
            import('./au.com.seven').then(({default: Provider}) => {
                window.provider = new Provider(partnerConfig.providers?.['au.com.seven']);
            });
            break;

        case 'au.com.nine':
            import('./au.com.nine').then(({default: Provider}) => {
                window.provider = new Provider(partnerConfig.providers?.['au.com.nine']);
            });
            break;

        case 'au.com.ten':
            import('./au.com.ten').then(({default: Provider}) => {
                window.provider = new Provider(partnerConfig.providers?.['au.com.ten']);
            });
            break;

        default:
            import('./magneto').then(({default: Provider}) => {
                window.provider = new Provider(partnerConfig.providers?.magneto);
            });
    }
}
