// So all this will run and calculate on boot, and then lazy load into this bundle.
// However, longer-than-now term, we may split these out into their own repos.
import {version as appVersion} from '../../package.json';
import {getPartnerIdConfig, getGlassIntegratedPlayerIframeManagerScriptUrlFromVersion} from '../utils/configs';
import {addProxyIp} from '../utils/utils';
import defaultMainIframeManager from './main';

window.isConsoleEnabled = window.localStorage.getItem('CONSOLE_ENABLED') || false;
if (!window.isConsoleEnabled) {
    for (const logFunction of ['log', 'info']) {
        // eslint-disable-next-line no-console, no-empty-function
        console[logFunction] = () => {};
    }
}

export default function init({
    preloadedPartnerConfig = undefined,
} = {}) {
    const queryParams = new URL(window.location).searchParams.entries();
    const {partnerId, estbMacAddress} = Object.fromEntries(queryParams);
    const partnerConfigUrl = getPartnerIdConfig(partnerId);

    // TODO MIPA-2 move this so that loader version is capture in appVersion
    if (window?.NREUM) {
        window.NREUM.setCustomAttribute('appVersion', appVersion);
        window.NREUM.setCustomAttribute('deviceEstbMacAddress', estbMacAddress);
        window.NREUM.setCustomAttribute('devicePartnerId', partnerId);
    }

    // if we don't have a partner id, don't load anything. Wait for further instructions.
    if (!partnerId) {
        return;
    }

    if (preloadedPartnerConfig) {
        return defaultMainIframeManager({partnerConfig: preloadedPartnerConfig});
    }

    let overrideVersion;

    fetch(addProxyIp(partnerConfigUrl))
        .then((response) => {
            if (response.ok) {
                return response.json();
            } else {
                throw `iframeManager unable to get partner config. ${partnerConfigUrl} ${response.status} ${response.statusText}`;
            }
        })
        .then((partnerConfig) => {
            // eslint-disable-next-line no-console
            console.log(`partnerConfig fetched ${JSON.stringify(partnerConfig)}`);

            // If development build, never try for a different version.  Use local main.
            if (process.env.NODE_ENV === 'development') {
                return defaultMainIframeManager({partnerConfig});
            }

            // Do we have a version to use from config?
            if (partnerConfig?.version) {
                overrideVersion = partnerConfig.version;
            }

            // Do we have a version to use from MAC address overrides?  This more pressing than global version.
            if (estbMacAddress && partnerConfig?.macAddressOverrides?.[estbMacAddress]) {
                overrideVersion = partnerConfig.macAddressOverrides[estbMacAddress];
            }

            // If we don't have an override version, or we do and it's the same to the one loaded right now, just use local main.
            if (!overrideVersion || overrideVersion === appVersion) {
                return defaultMainIframeManager({partnerConfig});
            }

            // finish doing stuff if version is different / fetch iframe manager, put on page, etc.
            return new Promise((resolve, reject) => {
                const script = document.createElement('script');

                script.src = getGlassIntegratedPlayerIframeManagerScriptUrlFromVersion(overrideVersion);
                script.type = 'module';
                script.addEventListener('load', resolve);
                script.addEventListener('error', (e) => reject(e));
                document.head.appendChild(script);
            })
                .then(() => {
                    // When successful, pass all our currently open things to the new version of ourselves exposed on the dom (overwrites)
                    window.streamotionGlassIframeManager.init({
                        preloadedPartnerConfig: partnerConfig,
                    });
                })
                .catch((error) => {
                    // Any error loading the new script, pass to our local main.
                    console.error('iframeManager loadLatestVersionFromGlassConfigureObject', error);
                    defaultMainIframeManager({partnerConfig});
                });
        })
        .catch((error) => {
            console.error('iframeManager loadLatestVersionFromGlassConfigureObject Error in Fetch Partner Config', error);
            defaultMainIframeManager({partnerConfig: preloadedPartnerConfig});
        });
}

window.streamotionGlassIframeManager = {init};
